export default function uniCopy({ content, success, error }) {
  if (!content) return error(' !');
  content = typeof content === 'string' ? content : content.toString(); // 
  /**
   *   app
   */

  uni.setClipboardData({
    data: content,
    success: function() {
      success('');
      console.log('success');
    },
    fail: function() {
      success('');
    },
  });


  /**
   * H5
   */




















}
