//  api 
// https://ext.dcloud.net.cn/plugin?id=392
import Request from '@/utils/request';
import { DISCUZ_REQUEST_HOST } from '@/common/const';
import { i18n } from '@/locale';

import Vue from 'vue';


const http = new Request();
let tostTimeout;
let app;

/**
 * @description 
 * @param {Function}
 */
http.setConfig(config => {
  /* config  */
  Object.assign(config, {
    baseUrl: `${DISCUZ_REQUEST_HOST}api/`,
    header: {
      'Content-Type': 'application/vnd.api+json',
      Accept: 'application/vnd.api+json',
    },
  });

  Object.assign(config.header, {
    'X-App-Platform': 'wx_miniprogram',
  });

  return config;
});
http.validateStatus = statusCode => {
  return statusCode >= 200 && statusCode < 300;
};
/**
 * @param { Function} cancel - ,cancel catch() ; 
 *
 * @param {String} text ['handle cancel'| any] - catch((err) => {}) err.errMsg === 'handle cancel''handle cancel'
 * @cancel {Object} config - catch((err) => {}) err.config === config; requestconfig
 * function cancel(text, config) {}
 */
http.interceptor.request(conf => {
  const config = conf;

  if (config.custom.loading) {
    uni.showLoading({
      title: i18n.t('core.loading'),
      mask: true,
    });
  }


  app = Vue.prototype;






  // cancel cancel,catchreturn config
  try {
    const accessToken = app.$store.getters['session/get']('accessToken');

    if (accessToken && config.custom.login) {
      // eslint-disable-next-line no-param-reassign
      config.header.Authorization = `Bearer ${accessToken}`;
    } else {
      delete config.header.Authorization;
    }
  } catch (e) {
    // error
  }
  return config;
});

// 
http.interceptor.response(
  response => {
    if (response.config.custom.loading) {
      uni.hideLoading();
    }
    //  >= 200 < 300 
    response.status = response.statusCode;
    return response;
  },
  response => {
    if (response.config.custom.loading) {
      uni.hideLoading();
    }


    app = getApp().$vm;






    //  statusCode !== 200return response
    if (response && response.data && response.data.errors) {
      response.data.errors.forEach(error => {
        switch (error.code) {
          case 'access_denied':
            // token  
            delete response.config.header.Authorization;
            break;
          case 'model_not_found':
            console.log('');
            app.$store.dispatch('forum/setError', {
              code: 'type_404',
              status: 500,
            });
            break;
          case 'permission_denied':
            console.log('');
            app.$store.dispatch('forum/setError', {
              code: 'type_401',
              status: 500,
            });
            break;
          case 'site_closed':
            uni.showToast({
              icon: 'none',
              title: i18n.t(`core.${error.code}`),
            });
            break;
          case 'not_install':
          case 'ban_user':
          case 'no_bind_user':
          case 'content_banned_show_words':
            break;
          default:
            if (response.config.custom.showTost) {
              clearTimeout(tostTimeout);
              tostTimeout = setTimeout(() => {
                // eslint-disable-next-line no-nested-ternary
                const title = error.detail
                  ? Array.isArray(error.detail)
                    ? error.detail[0]
                    : error.detail
                  : i18n.t(`core.${error.code}`);
                uni.showToast({
                  icon: 'none',
                  title,
                });
              });
            }
        }
      });
    } else if (response.config.custom.showTost) {
      uni.showToast({ title: response.errMsg });
    }
    return response;
  },
);

export { http };
