/**
 * Request 1.0.6
 * @Class Request
 * @description luch-request 1.0.6 http
 * @Author lu-ch
 * @Date 2020-03-17
 * @Email webwork.s@qq.com
 * http://ext.dcloud.net.cn/plugin?id=392
 */
export default class Request {
  config = {
    baseUrl: '',
    header: {
      'content-type': 'application/json',
    },
    method: 'GET',
    dataType: 'json',

    responseType: 'text',

    custom: {
      loading: true,
      showTost: true,
      login: true,
    },






  };

  static posUrl(url) {
    /* url */
    return /(http|https):\/\/([\w.]+\/?)\S*/.test(url);
  }

  static mergeUrl(url, baseUrl, params) {
    let mergeUrl = Request.posUrl(url) ? url : `${baseUrl}${url}`;
    if (Object.keys(params).length !== 0) {
      const paramsH = Request.addQueryString(params);
      mergeUrl += mergeUrl.includes('?') ? `&${paramsH}` : `?${paramsH}`;
    }
    return mergeUrl;
  }

  static addQueryString(params) {
    let paramsData = '';
    Object.keys(params).forEach(function(key) {
      paramsData += `${key}=${encodeURIComponent(params[key])}&`;
    });
    return paramsData.substring(0, paramsData.length - 1);
  }

  /**
   * @property {Function} request 
   * @property {Function} response 
   * @type {{request: Request.interceptor.request, response: Request.interceptor.response}}
   */
  interceptor = {
    /**
     * @param {Request~requestCallback} cb - ,config, cancel=> {return config}config,
     */
    request: cb => {
      if (cb) {
        this.requestBeforeFun = cb;
      }
    },
    /**
     * @param {Request~responseCallback} cb 
     * @param {Request~responseErrCallback} ecb 
     */
    response: (cb, ecb) => {
      if (cb) {
        this.requestComFun = cb;
      }
      if (ecb) {
        this.requestComFail = ecb;
      }
    },
  };

  requestBeforeFun(config) {
    return config;
  }

  requestComFun(response) {
    return response;
  }

  requestComFail(response) {
    return response;
  }

  /**
   * true (resolve)(reject)
   * @param { Number } statusCode - statusCode
   * @return { Boolean } true, resolve,  reject
   */
  validateStatus(statusCode) {
    return statusCode === 200;
  }

  /**
   * @Function
   * @param {Request~setConfigCallback} f - 
   */
  setConfig(f) {
    this.config = f(this.config);
  }

  /**
   * @Function
   * @param {Object} options - 
   * @prop {String} options.url - 
   * @prop {Object} options.data - 
   * @prop {Object} [options.responseType = config.responseType] [text|arraybuffer] - 
   * @prop {Object} [options.dataType = config.dataType] -  json JSON.parse
   * @prop {Object} [options.header = config.header] - header
   * @prop {Object} [options.method = config.method] - 
   * @returns {Promise<unknown>}
   */
  async request(options = {}) {
    options.baseUrl = this.config.baseUrl;
    options.dataType = options.dataType || this.config.dataType;

    options.responseType = options.responseType || this.config.responseType;




    options.url = options.url || '';
    options.data = options.data || {};
    options.params = options.params || {};
    options.header = options.header || this.config.header;
    options.method = options.method || this.config.method;
    // 
    if (options.method === 'patch') {
      options.method = 'POST';
      options.header = { ...options.header, 'x-http-method-override': 'patch' };
    }
    options.custom = { ...this.config.custom, ...(options.custom || {}) };



    options.getTask = options.getTask || this.config.getTask;

    return new Promise((resolve, reject) => {
      let next = true;
      const cancel = (t = 'handle cancel', config = options) => {
        const err = {
          errMsg: t,
          config,
        };
        reject(err);
        next = false;
      };

      const handleRe = { ...this.requestBeforeFun(options, cancel) };
      const _config = { ...handleRe };



      if (!next) return;
      const requestTask = uni.request({
        url: Request.mergeUrl(_config.url, _config.baseUrl, _config.params),
        data: _config.data,
        header: _config.header,
        method: _config.method,



        dataType: _config.dataType,

        responseType: _config.responseType,




        complete: response => {
          response.config = handleRe;
          if (this.validateStatus(response.statusCode)) {
            // 
            response = this.requestComFun(response);
            resolve(response);
          } else {
            response = this.requestComFail(response);
            reject(response);
          }
        },
      });
      if (handleRe.getTask) {
        handleRe.getTask(requestTask, handleRe);
      }
    });
  }

  get(url, options = {}) {
    return this.request({
      url,
      method: 'GET',
      ...options,
    });
  }

  post(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'POST',
      ...options,
    });
  }

  // 
  patch(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'POST',
      ...options,
      header: {
        'x-http-method-override': 'patch',
      },
    });
  }


  put(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'PUT',
      ...options,
    });
  }




  delete(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'DELETE',
      ...options,
    });
  }




  connect(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'CONNECT',
      ...options,
    });
  }




  head(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'HEAD',
      ...options,
    });
  }




  options(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'OPTIONS',
      ...options,
    });
  }




  trace(url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'TRACE',
      ...options,
    });
  }



  upload(
    url,
    {






      file,
      filePath,
      name,
      header,
      formData = {},
      custom = {},
      params = {},
      getTask,
    },
  ) {
    return new Promise((resolve, reject) => {
      let next = true;
      const globalHeader = { ...this.config.header };
      delete globalHeader['content-type'];
      delete globalHeader['Content-Type'];
      const pubConfig = {
        baseUrl: this.config.baseUrl,
        url,



        filePath,
        file,
        method: 'UPLOAD',
        name,
        header: header || globalHeader,
        formData,
        params,
        custom: { ...this.config.custom, ...custom },
        getTask: getTask || this.config.getTask,
      };





      const cancel = (t = 'handle cancel', config = pubConfig) => {
        const err = {
          errMsg: t,
          config,
        };
        reject(err);
        next = false;
      };

      const handleRe = { ...this.requestBeforeFun(pubConfig, cancel) };
      const _config = {
        url: Request.mergeUrl(handleRe.url, handleRe.baseUrl, handleRe.params),



        filePath: handleRe.filePath,
        file: handleRe.file,
        name: handleRe.name,
        header: handleRe.header,
        formData: handleRe.formData,
        complete: response => {
          response.config = handleRe;
          if (typeof response.data === 'string') {
            response.data = JSON.parse(response.data);
          }
          if (this.validateStatus(response.statusCode)) {
            // 
            response = this.requestComFun(response);
            resolve(response);
          } else {
            response = this.requestComFail(response);
            reject(response);
          }
        },
      };





      if (!next) return;
      const requestTask = uni.uploadFile(_config);
      if (handleRe.getTask) {
        handleRe.getTask(requestTask, handleRe);
      }
    });
  }

  download(url, options = {}) {
    return new Promise((resolve, reject) => {
      let next = true;
      const pubConfig = {
        baseUrl: this.config.baseUrl,
        url,
        method: 'DOWNLOAD',
        header: options.header || this.config.header,
        params: options.params || {},
        custom: { ...this.config.custom, ...(options.custom || {}) },
        getTask: options.getTask || this.config.getTask,
      };
      const cancel = (t = 'handle cancel', config = pubConfig) => {
        const err = {
          errMsg: t,
          config,
        };
        reject(err);
        next = false;
      };

      const handleRe = { ...this.requestBeforeFun(pubConfig, cancel) };
      if (!next) return;
      const requestTask = uni.downloadFile({
        url: Request.mergeUrl(handleRe.url, handleRe.baseUrl, handleRe.params),
        header: handleRe.header,
        complete: response => {
          response.config = handleRe;
          if (this.validateStatus(response.statusCode)) {
            // 
            response = this.requestComFun(response);
            resolve(response);
          } else {
            response = this.requestComFail(response);
            reject(response);
          }
        },
      });
      if (handleRe.getTask) {
        handleRe.getTask(requestTask, handleRe);
      }
    });
  }
}

/**
 * setConfig
 * @return {Object} - config
 * @callback Request~setConfigCallback
 * @param {Object} config - config
 */
/**
 * 
 * @return {Object} - config
 * @callback Request~requestCallback
 * @param {Object} config - config
 * @param {Function} [cancel] - 
 */
/**
 * 
 * @return {Object} - response
 * @callback Request~responseCallback
 * @param {Object} response -  response
 */
/**
 * 
 * @return {Object} - response
 * @callback Request~responseErrCallback
 * @param {Object} response -  response
 */
