import user from '@/mixin/user';
import { SITE_PAY } from '@/common/const';




module.exports = {
  mixins: [
    user,



  ],
  methods: {
    /**
     * 
     */
    getForum() {
      this.$store.dispatch('jv/get', ['forum', { params: { include: 'users' } }]).then(res => {
        if (res) {
          this.forum = res;
        }
      });
    },
    /**
     * 
     */
    jump2PhoneLoginPage() {
      const url = '/pages/user/phone-login';
      uni.redirectTo({
        url,
      });
    },
    /**
     * /
     */
    jump2PhoneLoginRegisterPage() {
      const url = '/pages/user/phone-login-register';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2LoginPage() {
      const url = '/pages/user/login';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2RegisterPage() {
      const url = '/pages/user/register';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2LoginBindPage() {
      const url = '/pages/user/login-bind';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2RegisterBindPage() {
      const url = '/pages/user/register-bind';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2LoginBindPhonePage() {
      const url = '/pages/user/login-bind-phone';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2RegisterBindPhonePage() {
      const url = '/pages/user/register-bind-phone';
      uni.redirectTo({
        url,
      });
    },
    /**
     * 
     */
    jump2findpwdPage() {
      const url = `/pages/modify/findpwd?pas=reset_pwd`;
      uni.navigateTo({
        url,
      });
    },
    /**
     * 
     */
    mpLoginMode() {
      if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 0) {
        // 
        this.jump2LoginPage();
      }
      if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 1) {
        // 
        this.jump2PhoneLoginRegisterPage();
      }
      if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 2) {
        // 
        uni.setStorageSync('register', 1);
        uni.setStorageSync('isSend', true);
        this.$store.getters['session/get']('auth').open();
      }
    },
    /**
     * h5
     */
    h5LoginMode() {
      const { isWeixin } = appCommonH.isWeixin();
      if (isWeixin) {
        // 
        if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 0) {
          // -
          uni.navigateTo({
            url: '/pages/user/login',
          });
        }
        if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 1) {
          // -
          this.jump2PhoneLoginRegisterPage();
        }
        if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 2) {
          uni.setStorageSync('register', 1);
          // -
          this.$store.dispatch('session/wxh5Login');
        }
      } else {
        // 
        if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 0) {
          // -
          uni.navigateTo({
            url: '/pages/user/login',
          });
        }
        if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 1) {
          // -
          this.jump2PhoneLoginRegisterPage();
        }
        if (this.forums && this.forums.set_reg && this.forums.set_reg.register_type === 2) {
          // -
          uni.navigateTo({
            url: '/pages/user/login',
          });
        }
      }
    },
    /**
     * 
     */
    refreshmpParams() {
      uni.login({
        success: loginRes => {
          if (loginRes.errMsg === 'login:ok') {
            const { code } = loginRes;
            uni.getUserInfo({
              success: res => {
                const params = {
                  data: {
                    attributes: {
                      js_code: code,
                      iv: res.iv,
                      encryptedData: res.encryptedData,
                    },
                  },
                };
                this.$store.dispatch('session/setParams', params);
              },
              fail: error => {
                console.log(error);
              },
            });
          }
        },
        fail: error => {
          console.log(error);
        },
      });
    },
    /**
     * /
     */
    mpLogin(register = 0) {
      uni.setStorageSync('register', register);
      uni.setStorageSync('isSend', true);
      uni.setStorageSync('isBind', false);
      this.$store.getters['session/get']('auth').open();
    },
    /**
     * h5/
     */
    wxh5Login(register = 0, rebind = 0) {
      uni.setStorageSync('register', register);
      uni.setStorageSync('rebind', rebind);
      this.$store.dispatch('session/wxh5Login');
    },
    /**
     * 
     * @param {Object} param 
     * @param {*} resultDialog 
     */
    getLoginParams(param, resultDialog) {
      const params = param;
      if (param.data.attributes.username === '') {
        uni.showToast({
          icon: 'none',
          title: this.i18n.t('user.usernameEmpty'),
          duration: 2000,
        });
      } else if (param.data.attributes.password === '') {
        uni.showToast({
          icon: 'none',
          title: this.i18n.t('user.passwordEmpty'),
          duration: 2000,
        });
      } else {
        const token = uni.getStorageSync('token');
        if (token !== '') {
          params.data.attributes.token = token;
        }
        this.login(params, resultDialog);
      }
    },
    /**
     * 
     * @param {Object} param 
     * @param {*} resultDialog 
     * @param {num} rebind 10
     */
    getLoginBindParams(param, resultDialog, rebind = 0) {
      this.refreshmpParams();
      const params = param;
      if (param.data.attributes.username === '') {
        uni.showToast({
          icon: 'none',
          title: this.i18n.t('user.usernameEmpty'),
          duration: 2000,
        });
      } else if (param.data.attributes.password === '') {
        uni.showToast({
          icon: 'none',
          title: this.i18n.t('user.passwordEmpty'),
          duration: 2000,
        });
      } else {

        // 
        const data = this.$store.getters['session/get']('params');
        if (data && data.data && data.data.attributes) {
          params.data.attributes.js_code = data.data.attributes.js_code;
          params.data.attributes.iv = data.data.attributes.iv;
          params.data.attributes.encryptedData = data.data.attributes.encryptedData;
        }

        if (rebind === 1) {
          params.data.attributes.rebind = 1;
        }
        const token = uni.getStorageSync('token');
        if (token !== '') {
          params.data.attributes.token = token;
        }
        this.login(params, resultDialog);
      }
    },
    /**
     * 
     */
    login(params, resultDialog) {
      this.$store
        .dispatch('session/h5Login', params)
        .then(res => {
          if (res && res.data && res.data.data && res.data.data.id) {

            this.refreshmpParams();

            this.logind();
            this.$store
              .dispatch('jv/get', ['forum', { params: { include: 'users' } }])
              .then(forumRes => {
                if (forumRes && forumRes.set_site && forumRes.set_site.site_mode !== SITE_PAY) {
                  uni.getStorage({
                    key: 'page',
                    success(resData) {
                      console.log('resData', resData);
                      uni.redirectTo({
                        url: resData.data,
                      });
                    },
                  });
                }
                if (
                  forumRes &&
                  forumRes.set_site &&
                  forumRes.set_site.site_mode === SITE_PAY &&
                  this.user &&
                  !this.user.paid
                ) {
                  uni.redirectTo({
                    url: '/pages/site/info',
                  });
                }
              });
            uni.showToast({
              title: resultDialog,
              duration: 2000,
            });
          }
          if (res && res.data && res.data.errors) {

            this.refreshmpParams();

            if (
              res.data.errors[0].status === '401' ||
              res.data.errors[0].status === '402' ||
              res.data.errors[0].status === '500'
            ) {
              const title = this.i18n.t(`core.${res.data.errors[0].code}`);
              uni.showToast({
                icon: 'none',
                title,
                duration: 2000,
              });
            }
            if (res.data.errors[0].status === '403' || res.data.errors[0].status === '422') {
              const title = this.i18n.t(res.data.errors[0].detail[0]);
              uni.showToast({
                icon: 'none',
                title,
                duration: 2000,
              });
            }
          }
        })
        .catch(err => console.log(err));
    },
  },
};
